import { ContentBlockDiv } from './content-block-div';
import { ContentBlockString } from './content-block-string';
import { ContentBlockName, ContentBlockIO } from '../../../../api/test.api';
import { ContentBlockBase } from './content-block-base';
import { ContentBlockCard } from './content-block-card';
import { ContentBlockCardSection } from './content-block-card-section';
import { ContentBlockStack } from './content-block-stack';
import { ContentBlockReactFragment } from './content-block-react-fragment';
import { ContentBlockText } from './content-block-text';
import { ContentBlockLink } from './content-block-link';
import { ContentBlockImg } from './content-block-img';

export const getBlockFromName = (contentBlockName: ContentBlockName) => {
    switch (contentBlockName) {
        case ContentBlockName.ReactFragment:
            return new ContentBlockReactFragment();
        case ContentBlockName.string:
            return new ContentBlockString();
        case ContentBlockName.div:
            return new ContentBlockDiv();
        case ContentBlockName.Stack:
            return new ContentBlockStack();
        case ContentBlockName.Card:
            return new ContentBlockCard();
        case ContentBlockName.CardSection:
            return new ContentBlockCardSection();
        case ContentBlockName.Text:
            return new ContentBlockText();
        case ContentBlockName.Link:
            return new ContentBlockLink();
        case ContentBlockName.img:
            return new ContentBlockImg();
        default:
            return new ContentBlockReactFragment();
    }
};

export const createFromContentBlockIO = (contentBlockIO: ContentBlockIO) => {
    const contentBlock: ContentBlockBase = getBlockFromName(contentBlockIO.contentBlockName);
    contentBlock.initParams(contentBlockIO.parameters);

    if (contentBlockIO.children) {
        contentBlockIO.children.forEach(childContentBlockIO => {
            const child = createFromContentBlockIO(childContentBlockIO);
            contentBlock.addChild(child);
        });
    }

    return contentBlock;
};
