import React from 'react';
import { Template } from '../../../../../stores/template-editor.store';
import { observer } from 'mobx-react';
import { templateEditorStore as store } from '../../../../../stores';
import Styles from './template-list.module.css';
import { DropdownProps, Select } from '@servicetitan/design-system';
import memoizeOne from 'memoize-one';

const memoizedOptions = memoizeOne((templates: Template[]) =>
    templates.map(template => ({
        key: template.name,
        value: template.name,
        text: template.name,
    }))
);

export const TemplateList: React.FC = observer(() => {

    const options = memoizedOptions(store.templates);

    const handleChange = (_1: React.SyntheticEvent, data: DropdownProps) => {
        const template = store.templates.find(template => template.name === data.value);
        if (template) {
            store.selectTemplate(template);
        }
    };

    return (
        <div className={Styles.templateList}>
            <Select
                value={store.selectedTemplate?.name}
                placeholder="Select template"
                options={options}
                onChange={handleChange}
            />
        </div>
    );
});
