import React from 'react';
import { ParamKind, Parameter, ParameterProps } from '../block-parameters-definition';
import { observable, action } from 'mobx';
import { observer } from 'mobx-react';
import { Form, InputOnChangeData } from '@servicetitan/design-system';

export class ParameterString implements Parameter {
    paramKind = ParamKind.string;
    name = '';
    @observable value?: string;

    InputValueComponent: React.FC = observer(() => {
        return (
            <Form>
                <Form.Input value={this.value} onChange={this.handleChange} small />
            </Form>
        );
    });

    constructor(props: ParameterProps) {
        this.name = props.parameterName;
        if (props.value) {
            this.setValue(props.value);
        }
    }

    @action
    setValue(value: string) {
        this.value = value;
    }

    handleChange = (_1: React.ChangeEvent<HTMLInputElement>, data: InputOnChangeData) => {
        this.setValue(data.value);
    };

    toObject() {
        return !this.value
            ? {}
            : {
                  [this.name]: this.value,
              };
    }
}
