import React from 'react';
import { TemplateEditorStore } from '../../../../../stores/template-editor.store';
import { observer } from 'mobx-react';
import Styles from './content-block-structure.module.css';
import { ContentBlock } from '../../../../common/constants/content-block-definitions';
import classNames from 'classnames';
import { templateEditorStore as store } from '../../../../../stores';

interface ContentBlockStructureProps {
    contentBlock: ContentBlock;
}

export const ContentBlockStructure: React.FC<ContentBlockStructureProps> = observer(props => {
    const selectComponentBlockEvent = new Event(store.selectComponentBlockEventName);

    const handleClick = (event: React.MouseEvent) => {
        event.stopPropagation();
        store.selectContentBlock(props.contentBlock);
        window.dispatchEvent(selectComponentBlockEvent);
    };

    const handleMouseOver = (event: React.MouseEvent) => {
        event.stopPropagation();
        store.selectHoveredContentBlock(props.contentBlock);
    };

    const handleMouseOut = (event: React.MouseEvent) => {
        event.stopPropagation();
        store.forgetHoveredContentBlock();
    };

    const className = store.selectedContentBlock === props.contentBlock ? 'bg-cyan-100' : '';

    const description = props.contentBlock.parameters
        ? props.contentBlock.parameters
            .filter(param => param.value)
            .map(param => (param.value === true ? param.name : param.value))
            .join(', ')
        : '';

    return (
        <div className={Styles.contentBlockStructure}>
            <div
                className={classNames(className, Styles.contentBlockName)}
                onClick={handleClick}
                onMouseOver={handleMouseOver}
                onMouseOut={handleMouseOut}
            >
                <b>{props.contentBlock.contentBlockName}:</b> {description.substr(0, 75)}
            </div>
            {props.contentBlock.childrenBlocks.map((childBlock, key) => (
                // eslint-disable-next-line
                <ContentBlockStructure key={key} contentBlock={childBlock} />
            ))}
        </div>
    );
});
