import { ContentBlockBase } from './content-block-base';
import { ParamKind, paramKindCreationFuncs } from '../block-parameters-definition';
import { Card } from '@servicetitan/design-system';
import { ContentBlockName, ParametersIO } from '../../../../api/test.api';

export class ContentBlockCardSection extends ContentBlockBase {
    BaseComponent = Card.Section;

    constructor() {
        super(ContentBlockName.CardSection);
    }

    initParams = (iParametersIO?: ParametersIO) => {
        const iParamsIO = iParametersIO ?? {};
        const colorArr = ['blue', 'dark', 'green', 'light', 'purple', 'red', 'lightblue'];

        const colorFunc = paramKindCreationFuncs(ParamKind.singleSelectString)!;
        const colorParameter = colorFunc({
            parameterName: 'color',
            paramArray: colorArr,
            value: iParamsIO.color,
        });

        const classNameFunc = paramKindCreationFuncs(ParamKind.string)!;
        const className = classNameFunc({ parameterName: 'className', value: iParamsIO.className });

        this.parameters = [colorParameter, className];
    };
}
