import React from 'react';
import { ParamKind, Parameter, ParameterProps } from '../block-parameters-definition';
import { observable, action } from 'mobx';
import { observer } from 'mobx-react';
import { Form, DropdownProps } from '@servicetitan/design-system';

interface SelectOption {
    key: string;
    value: string;
    text: string;
}

export class ParameterOneOfParamArray implements Parameter {
    paramKind = ParamKind.string;
    name = '';
    @observable value?: string;
    selectOptions: SelectOption[];

    InputValueComponent: React.FC = observer(() => {
        return (
            <Form.Select
                clearable
                value={this.value}
                onChange={this.handleChange}
                options={this.selectOptions}
                small
            />
        );
    });

    constructor(props: ParameterProps) {
        this.name = props.parameterName;
        if (props.value) {
            this.setValue(props.value);
        }
        const paramArray = props.paramArray ?? [];
        this.selectOptions = paramArray.map(param => ({ key: param, value: param, text: param }));
    }

    @action
    setValue(value: string) {
        this.value = value;
    }

    handleChange = (_1: React.SyntheticEvent, data: DropdownProps) => {
        this.setValue(data.value as string);
    };

    toObject() {
        return this.value === undefined
            ? {}
            : {
                  [this.name]: this.value,
              };
    }
}
