import { ContentBlockBase } from './content-block-base';
import {
    arrayOfParamKindBoolean,
    ParamKind,
    paramKindCreationFuncs,
} from '../block-parameters-definition';
import { Text } from '@servicetitan/design-system';
import { ContentBlockName, ParametersIO } from '../../../../api/test.api';

export class ContentBlockText extends ContentBlockBase {
    BaseComponent = Text;

    constructor() {
        super(ContentBlockName.Text);
    }

    initParams = (iParametersIO?: ParametersIO) => {
        const iParamsIO = iParametersIO ?? {};
        const booleanParamArray = ['bold', 'inline', 'italic', 'subdued'];
        const booleanParams = arrayOfParamKindBoolean(booleanParamArray, iParametersIO);

        const sizeFunc = paramKindCreationFuncs(ParamKind.singleSelectString)!;
        const sizeParameter = sizeFunc({
            parameterName: 'size',
            paramArray: ['1', '2', '3', '4', '5', '6', '7'],
            value: iParamsIO.size,
        });

        const classNameFunc = paramKindCreationFuncs(ParamKind.string)!;
        const className = classNameFunc({ parameterName: 'className', value: iParamsIO.className });

        this.parameters = booleanParams.concat([sizeParameter, className]);
    };
}
