import { ContentBlockBase } from './content-block-base';
import {
    arrayOfParamKindString,
    ParamKind,
    paramKindCreationFuncs,
} from '../block-parameters-definition';
import { ContentBlockName, ParametersIO } from '../../../../api/test.api';
import React from 'react';
import { observer } from 'mobx-react';

export class ContentBlockImg extends ContentBlockBase {
    Component = observer((props?: Record<string, any>) => {
        const onClick = props?.editorClick
            ? { onClick: this.handleEditorClick(props.editorClick) }
            : {};

        return <img {...props} {...onClick} {...this.paramsObject} />;
    });

    constructor() {
        super(ContentBlockName.img);
    }

    initParams = (parametersIO?: ParametersIO) => {
        const paramsIO = parametersIO ?? {};

        const stringParamArray = ['src', 'className', 'width', 'height', 'alt', 'hspace', 'vspace'];
        const stringParams = arrayOfParamKindString(stringParamArray, parametersIO);

        const alignFunc = paramKindCreationFuncs(ParamKind.oneOfParamArray)!;
        const alignParam = alignFunc({
            parameterName: 'align',
            paramArray: ['bottom', 'left', 'middle', 'right', 'top'],
            value: paramsIO.align,
        });

        const styleFunc = paramKindCreationFuncs(ParamKind.JSON)!;
        const styleParam = styleFunc({
            parameterName: 'style',
            value: paramsIO.style,
        });

        this.parameters = stringParams.concat(alignParam, styleParam);
    };

    addChild = () => {
        // console.log('"string" content block can not has children');
    };
}
