import React from 'react';
import { ContentBlockBase } from './content-block-base';
import { ParamKind, paramKindCreationFuncs } from '../block-parameters-definition';
import { ContentBlockName, ParametersIO } from '../../../../api/test.api';

export class ContentBlockDiv extends ContentBlockBase {
    constructor() {
        super(ContentBlockName.div);
    }

    BaseComponent = (props?: { [name: string]: any }) => <div {...props} />;

    initParams = (parameters?: ParametersIO) => {
        const params = parameters ?? {};

        const classNameFunc = paramKindCreationFuncs(ParamKind.string);
        if (classNameFunc) {
            const className = classNameFunc({
                parameterName: 'className',
                value: params.className,
            });

            const styleFunc = paramKindCreationFuncs(ParamKind.JSON)!;
            const styleParam = styleFunc({
                parameterName: 'style',
                value: params.style,
            });

            this.parameters = [className, styleParam];
        }
    };
}
