import React, { useState } from 'react';
import { observer } from 'mobx-react';
import { ButtonGroup, Button, Modal, Layout } from '@servicetitan/design-system';
import { ContentBlockName } from '../../../../../api/test.api';
import { templateEditorStore as store } from '../../../../../stores/template-editor.store';

export const ActionsPanel: React.FC = observer(() => {
    const [visibleAddChildModal, setVisibleAddChildModal] = useState(false);

    const openAddChildModal = () => {
        setVisibleAddChildModal(true);
    };

    const handleAddChild = () => {
        if (!store.selectedContentBlock) {
            return;
        }
        openAddChildModal();
    };

    const closeAddChildModal = () => {
        setVisibleAddChildModal(false);
    };

    const handleMoveUp = () => {
        store.moveUpSelectedContentBlock();
    };

    const handleMoveDown = () => {
        store.moveDownSelectedContentBlock();
    };

    const handleDelete = () => {
        store.deleteSelectedContentBlock();
    };

    const selectChildContentBlockName = (contentBlockName: ContentBlockName) => () => {
        closeAddChildModal();
        store.addChildForSelectedContentBlock(contentBlockName);
    };

    return (
        <React.Fragment>
            <ButtonGroup attached>
                <Button onClick={handleAddChild}>Add child</Button>
                <Button onClick={handleMoveUp}>Move Up</Button>
                <Button onClick={handleMoveDown}>Move Down</Button>
                <Button onClick={handleDelete}>Delete</Button>
            </ButtonGroup>

            <Modal
                title="Select Content Block Name:"
                closable
                open={visibleAddChildModal}
                onClose={closeAddChildModal}
            >
                <Layout type="2Col">
                    {Object.values(ContentBlockName).map(blockName => (
                        <Layout.Section key={blockName}>
                            <Button small onClick={selectChildContentBlockName(blockName)}>
                                {blockName}
                            </Button>
                        </Layout.Section>
                    ))}
                </Layout>
            </Modal>
        </React.Fragment>
    );
});
