// import { inject, injectable } from '@servicetitan/react-ioc';
import { action, observable } from 'mobx';
import { inject } from 'mobx-react';
import { ContentBlock } from '../modules/common/constants/content-block-definitions';
import {
    TestApi,
    ContentBlockName,
    ContentBlockIO,
    TemplateIO,
} from '../api/test.api';
import {
    createFromContentBlockIO,
    getBlockFromName,
} from '../modules/common/constants/content-blocks';
import React from 'react';
import { withClassNameHOC } from '../modules/admin/template-editor/utils/withClassNameHOC';
import styles from '../modules/admin/template-editor/components/selected-template/selected-template.module.css';

import { mockTemplates } from '../api/mockTemplates';
import { testApi as contentApi } from './index';

export interface Template extends TemplateIO {
    contentBlock: ContentBlock;
}

// @injectable()
export class TemplateEditorStore {
    @observable counter = 0;
    kuku = 'kukushka';
    @observable data = 'ContentManagerStore data';
    @observable templates: Template[] = [];
    @observable selectedTemplate?: Template;
    @observable textareaCopy?: string;
    @observable textareaCopyError = false;
    @observable templateContentBlockCopy?: ContentBlock;
    @observable selectedContentBlock?: ContentBlock;
    @observable selectedContentBlockComponent?: React.ComponentType;
    @observable hoveredContentBlock?: ContentBlock;
    @observable hoveredContentBlockComponent?: React.ComponentType;

    selectComponentBlockEventName = 'selectComponentBlockEventName';

    incCounter = () => this.counter += 1;
    
    constructor() {
        this.init();
    }

    @action
    init = async () => {
        const templates = await contentApi.loadTemplates();
        console.dir(templates);

        this.templates = templates.map(template => ({
            ...template,
            contentBlock: createFromContentBlockIO(template.contentBlockIO),
        }));

        this.initDummyData();
    };

    @action
    addTemplate = (iTemplateIO: TemplateIO) => {
        const template: Template = {
            ...iTemplateIO,
            contentBlock: createFromContentBlockIO(iTemplateIO.contentBlockIO),
        };
        this.templates.push(template);
    };

    saveTemplates = async () => {
        await contentApi.saveTemplates(
            this.templates.map(template => ({
                name: template.name,
                description: template.description,
                contentBlockIO: template.contentBlockIO,
            }))
        );
    };

    @action
    selectTemplate = (template: Template) => {
        this.selectedTemplate = template;
        this.selectContentBlock(template.contentBlock);
    };

    @action
    selectContentBlock = (contentBlock: ContentBlock) => {
        this.forgetHoveredContentBlock();

        if (this.selectedContentBlock && this.selectedContentBlockComponent) {
            this.selectedContentBlock.Component = this.selectedContentBlockComponent;
        }
        this.selectedContentBlockComponent = contentBlock.Component;
        this.selectedContentBlock = contentBlock;
        this.selectedContentBlock.Component = withClassNameHOC(
            contentBlock.Component,
            styles.selectedContentBlock
        );
    };

    @action
    addChildForSelectedContentBlock = (contentBlockName: ContentBlockName) => {
        if (!this.selectedContentBlock) {
            return;
        }

        const childBlock = getBlockFromName(contentBlockName);
        childBlock.initParams();
        this.selectedContentBlock.addChild(childBlock);
    };

    @action
    moveUpSelectedContentBlock = () => {
        if (!this.selectedContentBlock || !this.selectedContentBlock.parentBlock) {
            return;
        }

        const parentBlock = this.selectedContentBlock.parentBlock;
        const indexInParent = parentBlock.childrenBlocks.findIndex(
            block => block === this.selectedContentBlock
        );
        if (indexInParent > 0) {
            const prevBlock = parentBlock.childrenBlocks[indexInParent - 1];
            parentBlock.childrenBlocks.splice(
                indexInParent - 1,
                2,
                this.selectedContentBlock,
                prevBlock
            );
        }
    };

    @action
    moveDownSelectedContentBlock = () => {
        if (!this.selectedContentBlock || !this.selectedContentBlock.parentBlock) {
            return;
        }

        const parentBlock = this.selectedContentBlock.parentBlock;
        const indexInParent = parentBlock.childrenBlocks.findIndex(
            block => block === this.selectedContentBlock
        );
        if (indexInParent < parentBlock.childrenBlocks.length - 1) {
            const nextBlock = parentBlock.childrenBlocks[indexInParent + 1];
            parentBlock.childrenBlocks.splice(
                indexInParent,
                2,
                nextBlock,
                this.selectedContentBlock
            );
        }
    };

    @action
    deleteSelectedContentBlock = () => {
        if (this.selectedContentBlock && this.selectedContentBlock.parentBlock) {
            this.selectedContentBlock.deleteThisBlockFromParent();
        }
    };

    @action
    selectHoveredContentBlock = (contentBlock: ContentBlock) => {
        if (contentBlock === this.selectedContentBlock) {
            return;
        }

        if (this.hoveredContentBlock && this.hoveredContentBlockComponent) {
            this.hoveredContentBlock.Component = this.hoveredContentBlockComponent;
        }
        this.hoveredContentBlockComponent = contentBlock.Component;
        this.hoveredContentBlock = contentBlock;
        this.hoveredContentBlock.Component = withClassNameHOC(
            contentBlock.Component,
            styles.hoveredContentBlock
        );
    };

    @action
    forgetHoveredContentBlock = () => {
        if (this.hoveredContentBlock && this.hoveredContentBlockComponent) {
            this.hoveredContentBlock.Component = this.hoveredContentBlockComponent;
            this.hoveredContentBlockComponent = undefined;
            this.hoveredContentBlock = undefined;
        }
    };

    getJSONTextForBlock = (contentBlock: ContentBlock) =>
        JSON.stringify(contentBlock.toIContentBlockIO, null, '\t');

    @action
    handleChangeTextarea = (jsonText: string) => {
        try {
            this.textareaCopy = jsonText;
            const contentBlockIO: ContentBlockIO = JSON.parse(jsonText);
            this.textareaCopyError = false;
            this.templateContentBlockCopy = createFromContentBlockIO(contentBlockIO);
        } catch (e) {
            this.textareaCopyError = true;
            this.templateContentBlockCopy = undefined;
        }
    };

    getChildNumberOfParent = (contentBlock: ContentBlock): number => {
        if (!contentBlock.parentBlock) {
            return -1;
        }

        for (let i = 0; i < contentBlock.parentBlock.childrenBlocks.length; i += 1) {
            if (contentBlock.parentBlock.childrenBlocks[i] === contentBlock) {
                return i;
            }
        }

        return -1;
    };

    getChildrenChainFromTop = (contentBlock: ContentBlock) => {
        const childrenChain = [];
        let currentBlock = contentBlock;
        while (currentBlock.parentBlock) {
            childrenChain.push(this.getChildNumberOfParent(currentBlock));
            currentBlock = currentBlock.parentBlock;
        }
        return childrenChain;
    };

    getChildBlockByChildrenChain = (contentBlock: ContentBlock, childrenChain: number[]) => {
        let currentBlock = contentBlock;
        for (let i = childrenChain.length - 1; i >= 0; i -= 1) {
            if (childrenChain[i] === -1) {
                return currentBlock;
            }
            currentBlock = currentBlock.childrenBlocks[childrenChain[i]];
        }
        return currentBlock;
    };

    @action
    handleBlurTextarea = () => {
        if (this.templateContentBlockCopy) {
            this.selectedTemplate!.contentBlock = this.templateContentBlockCopy;
            this.templateContentBlockCopy = undefined;
        }
        this.textareaCopy = undefined;
        this.textareaCopyError = false;
    };

    initDummyData = () => {
        const ReactFragment = getBlockFromName(ContentBlockName.ReactFragment);

        const CardBlock = getBlockFromName(ContentBlockName.Card);
        CardBlock.initParams({ className: 'c-red' });

        const CardBlock2 = getBlockFromName(ContentBlockName.Card);
        CardBlock2.initParams({ className: 'c-magenta' });

        const CardSectionBlock = getBlockFromName(ContentBlockName.CardSection);
        CardSectionBlock.initParams({ color: 'dark' });

        const CardSectionBlock2 = getBlockFromName(ContentBlockName.CardSection);

        const CardSectionBlock3 = getBlockFromName(ContentBlockName.CardSection);
        CardSectionBlock3.initParams({ color: 'lightblue' });

        const divBlock = getBlockFromName(ContentBlockName.div);
        divBlock.initParams({ className: 'c-green' });

        const stringBlock = getBlockFromName(ContentBlockName.string);
        stringBlock.initParams({ string: 'kuku' });

        const stringBlock2 = getBlockFromName(ContentBlockName.string);
        stringBlock2.initParams({ string: 'la-la-la' });

        divBlock.addChild(stringBlock);

        CardSectionBlock.addChild(divBlock);
        CardSectionBlock2.addChild(stringBlock);
        CardSectionBlock3.addChild(stringBlock2);

        CardBlock.addChild(stringBlock2);
        CardBlock.addChild(CardSectionBlock);
        CardBlock.addChild(CardSectionBlock2);

        CardBlock2.addChild(CardSectionBlock);
        CardBlock2.addChild(CardSectionBlock2);
        CardBlock2.addChild(CardSectionBlock3);

        this.addTemplate({
            name: 'First #1',
            description: 'First example of template',
            contentBlockIO: CardBlock.toIContentBlockIO,
        });

        this.addTemplate({
            name: 'Second #2',
            description: 'Second example of template',
            contentBlockIO: CardBlock2.toIContentBlockIO,
        });

        this.addTemplate({
            name: 'Dummy Data example',
            description: 'Empty template',
            contentBlockIO: ReactFragment.toIContentBlockIO,
        });

        mockTemplates.forEach((contentBlockIO, key) => {
            this.addTemplate({
                name: `Mock template #${key}`,
                description: '',
                contentBlockIO: contentBlockIO as ContentBlockIO,
            });
        });

        this.saveTemplates();
    };
}

const templateEditorStore = new TemplateEditorStore();

export { templateEditorStore };
