import React from 'react';
import { ParamKind, Parameter, ParameterProps } from '../block-parameters-definition';
import { observable, action, toJS } from 'mobx';
import { observer } from 'mobx-react';
import { Form, InputOnChangeData } from '@servicetitan/design-system';

export class ParameterJSON implements Parameter {
    paramKind = ParamKind.JSON;
    name = '';
    @observable value?: object;
    @observable valueString?: string;

    InputValueComponent: React.FC = observer(() => {
        return (
            <Form>
                <Form.Input value={this.valueString} onChange={this.handleChange} small />
            </Form>
        );
    });

    constructor(props: ParameterProps) {
        this.name = props.parameterName;
        try {
            const valueString = JSON.stringify(props.value);
            if (props.value) {
                this.setValue(valueString);
            }
        } catch {
            // eslint-disable-next-line
        }
    }

    @action
    setValue(value: string) {
        this.valueString = value;
        try {
            this.value = JSON.parse(value);
        } catch (_1) {
            this.value = undefined;
        }
    }

    handleChange = (_1: React.ChangeEvent<HTMLInputElement>, data: InputOnChangeData) => {
        this.setValue(data.value);
    };

    toObject() {
        return !this.value
            ? {}
            : {
                  [this.name]: toJS(this.value),
              };
    }
}
