import React from 'react';
import { TemplateEditorStore } from '../../../../../stores/template-editor.store';
import { observer } from 'mobx-react';
import { templateEditorStore as store } from '../../../../../stores';

export const SelectedBlockParameters: React.FC = observer(() => {

    if (!store.selectedContentBlock || !store.selectedContentBlock.parameters) {
        return null;
    }

    const { parameters } = store.selectedContentBlock;

    return (
        <table className="w-100">
            <tbody>
                {parameters.map(({ name, InputValueComponent }) => (
                    <tr key={name}>
                        <td>{name}</td>
                        <td>
                            <InputValueComponent />
                        </td>
                    </tr>
                ))}
            </tbody>
        </table>
    );
});
