import { ContentBlockBase } from './content-block-base';
import {
    arrayOfParamKindBoolean,
    ParamKind,
    paramKindCreationFuncs,
} from '../block-parameters-definition';
import { Card } from '@servicetitan/design-system';
import { ContentBlockName, ParametersIO } from '../../../../api/test.api';

export class ContentBlockCard extends ContentBlockBase {
    BaseComponent = Card;

    constructor() {
        super(ContentBlockName.Card);
    }

    initParams = (iParametersIO?: ParametersIO) => {
        const booleanParamsName = [
            'active',
            'base',
            'dark',
            'error',
            'hoverable',
            'light',
            'raised',
            'sharp',
            'thin',
            'placeholder',
        ];

        const booleanParameters = arrayOfParamKindBoolean(booleanParamsName, iParametersIO);
        const iParamsIO = iParametersIO ?? {};

        const elevationFunc = paramKindCreationFuncs(ParamKind.singleSelectString)!;
        const elevation = elevationFunc({
            parameterName: 'elevation',
            paramArray: ['base', 'flat', 'raised'],
            value: iParamsIO.elevation,
        });

        const paddingFunc = paramKindCreationFuncs(ParamKind.singleSelectString)!;
        const padding = paddingFunc({
            parameterName: 'padding',
            paramArray: ['none', 'thin', 'default'],
            value: iParamsIO.padding,
        });

        const hrefFunc = paramKindCreationFuncs(ParamKind.string)!;
        const href = hrefFunc({ parameterName: 'href', value: iParamsIO.href });

        const statusFunc = paramKindCreationFuncs(ParamKind.singleSelectString)!;
        const status = statusFunc({
            parameterName: 'status',
            paramArray: ['default', 'active', 'error'],
            value: iParamsIO.status,
        });

        const classNameFunc = paramKindCreationFuncs(ParamKind.string)!;
        const className = classNameFunc({ parameterName: 'className', value: iParamsIO.className });

        this.parameters = booleanParameters.concat([elevation, padding, status, href, className]);
    };
}
