import * as React from 'react';
import { ParameterString } from './block-parameters/parameter-string';
import { ParameterOneOfParamArray } from './block-parameters/parameter-one-of-param-array';
import { ParameterBoolean } from './block-parameters/parameter-boolean';
import { ParameterSingleSelectString } from './block-parameters/parameter-single-select-string';
import { ParametersIO } from '../../../api/test.api';
import { ParameterJSON } from './block-parameters/parameter-json';

export enum ParamKind {
    string,
    singleSelectString,
    boolean,
    oneOfParamArray,
    JSON,
}

export interface Parameter {
    paramKind: ParamKind;
    name: string;
    value?: any;
    InputValueComponent: React.ComponentType;
    toObject(): Record<string, any>;
}

export interface ParameterProps {
    [name: string]: any;
    parameterName: string;
    paramArray?: string[];
    value?: any;
}

// ----------------------------------------------
export const paramKindCreationFuncs: (
    paramKind: ParamKind
) => (props: ParameterProps) => Parameter = (paramKind: ParamKind) => {
    switch (paramKind) {
        case ParamKind.string:
            return (props: ParameterProps) => new ParameterString(props);
        case ParamKind.singleSelectString:
            return (props: ParameterProps) => new ParameterSingleSelectString(props);
        case ParamKind.boolean:
            return (props: ParameterProps) => new ParameterBoolean(props);
        case ParamKind.oneOfParamArray:
            return (props: ParameterProps) => new ParameterOneOfParamArray(props);
        case ParamKind.JSON:
            return (props: ParameterProps) => new ParameterJSON(props);
        default:
            return (props: ParameterProps) => new ParameterString(props);
    }
};

// --------------------------------------------------------------------------------

export const arrayOfParamKindBoolean = (paramsName: string[], parametersIO?: ParametersIO) =>
    paramsName.map(parameterName =>
        paramKindCreationFuncs(ParamKind.boolean)({
            parameterName,
            value: parametersIO ? parametersIO[parameterName] : undefined,
        })
    );

export const arrayOfParamKindString = (paramsName: string[], parametersIO?: ParametersIO) =>
    paramsName.map(parameterName =>
        paramKindCreationFuncs(ParamKind.string)({
            parameterName,
            value: parametersIO ? parametersIO[parameterName] : undefined,
        })
    );
