import { ContentBlockBase } from './content-block-base';
import { ParamKind, paramKindCreationFuncs } from '../block-parameters-definition';
import { Stack } from '@servicetitan/design-system';
import { ContentBlockName, ParametersIO } from '../../../../api/test.api';

export class ContentBlockStack extends ContentBlockBase {
    BaseComponent = Stack;

    constructor() {
        super(ContentBlockName.Stack);
    }

    initParams = (iParametersIO?: ParametersIO) => {
        const iParamsIO = iParametersIO ?? {};

        const spacingFunc = paramKindCreationFuncs(ParamKind.singleSelectString)!;
        const spacing = spacingFunc({
            parameterName: 'spacing',
            paramArray: ['0', '1', '2', '3', '4', '5'],
            value: iParamsIO.spacing,
        });

        const wrapFunc = paramKindCreationFuncs(ParamKind.singleSelectString)!;
        const wrap = wrapFunc({
            parameterName: 'wrap',
            paramArray: ['nowrap', 'wrap', 'wrap-reverse'],
            value: iParamsIO.wrap,
        });

        const directionFunc = paramKindCreationFuncs(ParamKind.singleSelectString)!;
        const direction = directionFunc({
            parameterName: 'direction',
            paramArray: ['row', 'row-reverse', 'column', 'column-reverse'],
            value: iParamsIO.direction,
        });

        const justifyContentFunc = paramKindCreationFuncs(ParamKind.singleSelectString)!;
        const justifyContent = justifyContentFunc({
            parameterName: 'justifyContent',
            paramArray: [
                'flex-start',
                'flex-end',
                'center',
                'space-between',
                'space-around',
                'space-evenly',
            ],
            value: iParamsIO.justifyContent,
        });

        const alignItemsFunc = paramKindCreationFuncs(ParamKind.singleSelectString)!;
        const alignItems = alignItemsFunc({
            parameterName: 'alignItems',
            paramArray: ['stretch', 'flex-start', 'flex-end', 'center', 'baseline'],
            value: iParamsIO.alignItems,
        });

        const alignContentFunc = paramKindCreationFuncs(ParamKind.singleSelectString)!;
        const alignContent = alignContentFunc({
            parameterName: 'alignContent',
            paramArray: [
                'flex-start',
                'flex-end',
                'center',
                'space-between',
                'space-around',
                'stretch',
            ],
            value: iParamsIO.alignContent,
        });

        const classNameFunc = paramKindCreationFuncs(ParamKind.string)!;
        const className = classNameFunc({ parameterName: 'className', value: iParamsIO.className });

        this.parameters = [
            spacing,
            wrap,
            direction,
            justifyContent,
            alignItems,
            alignContent,
            className,
        ];
    };
}
