// super class for content blocks in Content Manager
import { Parameter } from '../block-parameters-definition';
import { ContentBlock, ParamsObject } from '../content-block-definitions';
import React from 'react';
import { ContentBlockName, ParametersIO, ContentBlockIO } from '../../../../api/test.api';
import { observable } from 'mobx';
import { observer } from 'mobx-react';
import classNames from 'classnames';

export class ContentBlockBase implements ContentBlock {
    parametersIO?: ParametersIO; // Each ContentBlock must initialize his parameters
    parentBlock?: ContentBlock;
    @observable childrenBlocks: ContentBlock[] = [];
    @observable parameters?: Parameter[] = [];

    @observable Component = observer((props: { [name: string]: any }) => {
        const className = classNames(this.paramsObject.className || '', props.className || '');

        const onClick = props.editorClick
            ? { onClick: this.handleEditorClick(props.editorClick) }
            : { onClick: props.onClick };

        return (
            <this.BaseComponent
                {...props}
                {...this.paramsObject}
                className={className}
                {...onClick}
            >
                {this.childrenBlocks.map((child, key) => (
                    <child.Component
                        // eslint-disable-next-line
                        key={key}
                        editorClick={props.editorClick}
                    />
                ))}
            </this.BaseComponent>
        );
    });

    constructor(public contentBlockName: ContentBlockName) {}

    initParams = (_1?: ParametersIO) => {}; // Each ContentBlock must has his own InitParams

    get paramsObject() {
        const res: ParamsObject = {};
        if (this.parameters) {
            this.parameters.forEach(param => {
                Object.assign(res, param.toObject());
            });
        }

        return res;
    }

    @observable BaseComponent: React.ComponentType<any> = () => null; // Each ContentBlock must has his own BaseComponent

    handleEditorClick(editorClick: (contentBlock: ContentBlock) => void) {
        return (event: React.SyntheticEvent) => {
            if (typeof event.stopPropagation === 'function') {
                event.stopPropagation();
            }
            editorClick(this);
        };
    }

    get toIContentBlockIO() {
        const children: ContentBlockIO[] | undefined = this.childrenBlocks.length
            ? this.childrenBlocks.map(block => block.toIContentBlockIO)
            : undefined;

        const parametersIO = {};
        if (this.parameters) {
            this.parameters.forEach(param => {
                Object.assign(parametersIO, param.toObject());
            });
        }

        const iContentBlock: ContentBlockIO = {
            contentBlockName: this.contentBlockName,
            parameters: Object.keys(parametersIO).length ? parametersIO : undefined,
            children,
        };

        return iContentBlock;
    }

    isChildForBlock = (block: ContentBlock) => {
        if (block.toIContentBlockIO === this.toIContentBlockIO) {
            return true;
        }

        if (!this.parentBlock) {
            return false;
        }

        let currentBlock: ContentBlock = this.parentBlock;
        do {
            if (currentBlock === block) {
                return true;
            }
            if (!currentBlock.parentBlock) {
                return false;
            }
            currentBlock = currentBlock.parentBlock;
        } while (currentBlock);

        return false;
    };

    addChild = (child: ContentBlock) => {
        if (this.isChildForBlock(child)) {
            return;
        }

        child.parentBlock = this;
        this.childrenBlocks.push(child);
    };

    deleteChild = (child: ContentBlockBase) => {
        for (let i = this.childrenBlocks.length - 1; i >= 0; i -= 1) {
            if (child === this.childrenBlocks[i]) {
                this.childrenBlocks.splice(i, 1);
            }
        }
    };

    deleteThisBlockFromParent = () => {
        if (!this.parentBlock) {
            // logError('can not delete this block from parent, cuz it have not parent block');
            return false;
        }

        this.parentBlock.deleteChild(this);
        return true;
    };

    clearBlock = () => {
        this.childrenBlocks = [];
    };
}
