import { observer } from 'mobx-react';
import { ContentBlockStructure } from '../content-block-structure/content-block-structure';
import { ActionsPanel } from '../actions-panel/actions-panel';
import React from 'react';
import { templateEditorStore as store } from '../../../../../stores';

export const SelectedTemplateStructure: React.FC = observer(() => {

    return store.selectedTemplate ? (
        <React.Fragment>
            <h3>Template &quot;{store.selectedTemplate?.name}&quot; structure:</h3>
            {store.textareaCopyError ? (
                <div>Bad structure JSON textarea</div>
            ) : (
                <ContentBlockStructure
                    contentBlock={
                        store.templateContentBlockCopy ?? store.selectedTemplate.contentBlock
                    }
                />
            )}
            <br />
            <ActionsPanel />
        </React.Fragment>
    ) : null;
});
