import React from 'react';
import { ContentBlockBase } from './content-block-base';
import { ContentBlockName } from '../../../../api/test.api';
import { observable } from 'mobx';
import { observer } from 'mobx-react';

export class ContentBlockReactFragment extends ContentBlockBase {
    @observable Component = observer((props: { [name: string]: any }) => {
        return (
            // eslint-disable-next-line
            <React.Fragment>
                {this.childrenBlocks.map((child, key) => (
                    <child.Component
                        // eslint-disable-next-line
                        key={key}
                        editorClick={props.editorClick}
                    />
                ))}
            </React.Fragment>
        );
    });

    constructor() {
        super(ContentBlockName.ReactFragment);
    }
}
