import React from 'react';
import { observer } from 'mobx-react';
import { templateEditorStore as store } from '../../../../../stores';
import Styles from './selected-template.module.css';
import { ContentBlock } from '../../../../common/constants/content-block-definitions';
import { Text } from '@servicetitan/design-system';

export const SelectedTemplate: React.FC = observer(() => {
    const selectComponentBlockEvent = new Event(store.selectComponentBlockEventName);

    if (!store.selectedTemplate) {
        return <div>Template is not selected...</div>;
    }

    if (store.textareaCopyError) {
        return <div>Error JSON structure in Textarea</div>;
    }

    const Component = store.selectedTemplate.contentBlock.Component;

    const handleClick = (contentBlock: ContentBlock) => {
        if (contentBlock !== store.selectedContentBlock) {
            store.selectContentBlock(contentBlock);
        }
        window.dispatchEvent(selectComponentBlockEvent);
    };

    return (
        <div>
            <Text size={4}>Component from received JSON:</Text>
            <div className={Styles.templateContainer}>
                {store.templateContentBlockCopy ? (
                    <store.templateContentBlockCopy.Component />
                ) : (
                    <Component editorClick={handleClick} />
                )}
            </div>
        </div>
    );
});
