// import { injectable } from '@servicetitan/react-ioc';
import { mockTemplates } from './mockTemplates';


export enum ContentBlockName {
    ReactFragment = 'ReactFragment',
    string = 'string',
    div = 'div',
    Stack = 'Stack',
    Card = 'Card',
    CardSection = 'CardSection',
    Text = 'Text',
    Link = 'Link',
    img = 'img',
}

// interface for store and load block's parameters as JSON (input/output)
export type ParametersIO = Record<string, any>;

// interface for store and load blocks as JSON (input/output)
export interface ContentBlockIO {
    contentBlockName: ContentBlockName;
    parameters?: ParametersIO;
    children?: ContentBlockIO[];
}

export interface TemplateIO {
    name: string;
    description: string;
    contentBlockIO: ContentBlockIO;
}

export enum BusinessGoal {
    DriveRevenue = 'Drive revenue',
    IncreaseEfficiency = 'Increase efficiency',
    ProvideExceptionalService = 'Provide exceptional service',
    GainBusinessInsights = 'Gain business insights',
}

export enum FeatureType {
    Setup,
    Usage,
}

export type FeatureId = number;

export interface Feature {
    id: FeatureId;
    type: FeatureType;
    name: string;
    description: string;
    template: TemplateIO;
    businessGoals: BusinessGoal[];
    ptsMax: number;
}

export enum SetupStatus {
    Todo = 0,
    InProgress,
    Done,
}

export enum UsageUtilization {
    None = 0,
    Poor,
    Ok,
    Great,
}

export interface TenantFeatureStatus {
    featureId: FeatureId;
    ptsValue: number;
    goalsValue: number;
    product: string;
    package: string;
    setupStatus: SetupStatus;
    usageUtilization: UsageUtilization;
    expectedMinutesToComplete: number;
}

export interface TenantStatus {
    recommendedFeatureIds: FeatureId[];
    featureStatuses: TenantFeatureStatus[];
    score: number;
    percentOfTitansHaveThisScore: number;
    setupsConfigured: number;
    setupsOverall: number;
    featuresUsedCorrectly: number;
    usageFeaturesOverall: number;
    businessGoalsOrder: BusinessGoal[];
}

//  -------------------------------- Mock data -------------------------------------

function mockTenantFeatureStatus(featureId = 1): TenantFeatureStatus {
    return {
        featureId,
        ptsValue: featureId + 1,
        goalsValue: 7,
        product: 'product text',
        package: 'package text',
        setupStatus:
            featureId === 1
                ? SetupStatus.Todo
                : featureId === 2
                ? SetupStatus.InProgress
                : SetupStatus.Done,
        usageUtilization:
            featureId === 1
                ? UsageUtilization.None
                : featureId === 2
                ? UsageUtilization.Poor
                : featureId === 3
                ? UsageUtilization.Ok
                : UsageUtilization.Great,
        expectedMinutesToComplete: 5 * Math.floor(1 + 10 * Math.random()),
    };
}

const mockTenantStatus: TenantStatus = {
    recommendedFeatureIds: [1, 2, 3, 4],
    featureStatuses: [
        mockTenantFeatureStatus(1),
        mockTenantFeatureStatus(2),
        mockTenantFeatureStatus(3),
        mockTenantFeatureStatus(4),
    ],
    score: 55,
    percentOfTitansHaveThisScore: 12,
    setupsConfigured: 3,
    setupsOverall: 33,
    featuresUsedCorrectly: 4,
    usageFeaturesOverall: 44,
    businessGoalsOrder: [
        BusinessGoal.DriveRevenue,
        BusinessGoal.GainBusinessInsights,
        BusinessGoal.ProvideExceptionalService,
        BusinessGoal.IncreaseEfficiency,
    ],
};

export const mockTemplate = (index = 0) => {
    const templateNumber = index % mockTemplates.length;
    return {
        name: 'Template name ' + Math.random(),
        description: 'Template description ' + Math.random(),
        contentBlockIO: mockTemplates[templateNumber] as ContentBlockIO,
    };
};

function mockFeature(featureId = 1): Feature {
    const nameArr = ['Enable online estimates', 'Engage with customers via chat'];
    const featureName =
        nameArr[featureId % nameArr.length] +
        '  ' +
        Math.random()
            .toString()
            .substr(2, 3);

    return {
        id: featureId,
        type: Math.random() < 0.5 ? FeatureType.Setup : FeatureType.Usage,
        name: featureName,
        description: 'Feature description ' + Math.random(),
        template: mockTemplate(featureId),
        businessGoals: [
            BusinessGoal.DriveRevenue,
            BusinessGoal.GainBusinessInsights,
            BusinessGoal.IncreaseEfficiency,
            BusinessGoal.ProvideExceptionalService,
        ].slice(Math.floor(Math.random() * 4)),
        ptsMax: featureId + 2,
    };
}

// ---------------------------------------------------------------------

// @injectable()
export class TestApi {
    loadTemplates = async () => {
        // await new Promise(resolve => resolve()); // this is temporary API
        const serializedTemplates = localStorage.getItem('templates') || '[]';
        try {
            const templates: TemplateIO[] =
                ((JSON.parse(serializedTemplates) as unknown) as TemplateIO[]) || [];
            return templates;
        } catch (e) {
            console.log(e);
            return [];
        }
    };

    saveTemplates = async (templates: TemplateIO[]) => {
        await new Promise(resolve => resolve()); // this is temporary API
        localStorage.templates = JSON.stringify(templates);
    };

    getTenantStatus = async () => {
        const tenantStatus: TenantStatus = await new Promise<TenantStatus>(resolve =>
            resolve(mockTenantStatus)
        );
        return tenantStatus;
    };

    getAdvisorFeature = async (featureId: FeatureId) => {
        const advisorFeature: Feature = await new Promise(resolve =>
            setTimeout(() => resolve(mockFeature(featureId)), 3000)
        );
        return advisorFeature;
    };
}

const testApi = new TestApi();
export { testApi };
