import { ContentBlockBase } from './content-block-base';
import {
    arrayOfParamKindBoolean,
    arrayOfParamKindString,
    ParamKind,
    paramKindCreationFuncs,
} from '../block-parameters-definition';
import { Link } from '@servicetitan/design-system';
import { ContentBlockName, ParametersIO } from '../../../../api/test.api';

export class ContentBlockLink extends ContentBlockBase {
    BaseComponent = Link;

    constructor() {
        super(ContentBlockName.Link);
    }

    initParams = (parametersIO?: ParametersIO) => {
        const paramsIO = parametersIO ?? {};

        const colorFunc = paramKindCreationFuncs(ParamKind.singleSelectString)!;
        const colorParameter = colorFunc({
            parameterName: 'color',
            paramArray: ['negative', 'red', 'primary', 'blue', 'subdued', 'grey'],
            value: paramsIO.color,
        });

        const booleanParamArray = ['negative', 'primary', 'subdued'];
        const booleanParams = arrayOfParamKindBoolean(booleanParamArray, parametersIO);

        const stringParamArray = ['href', 'className'];
        const stringParams = arrayOfParamKindString(stringParamArray, parametersIO);

        this.parameters = booleanParams.concat([colorParameter, ...stringParams]);
    };
}
