import React from 'react';
import { ContentBlockBase } from './content-block-base';
import { ParamKind, paramKindCreationFuncs } from '../block-parameters-definition';
import { ContentBlockName, ParametersIO } from '../../../../api/test.api';
import { observer } from 'mobx-react';

export class ContentBlockString extends ContentBlockBase {
    Component = observer((props?: { [name: string]: any }) => {
        const text = this.paramsObject.string || '';
        const onClick = props?.editorClick
            ? { onClick: this.handleEditorClick(props.editorClick) }
            : {};

        return props ? (
            <span {...props} {...onClick}>
                {text}
            </span>
        ) : (
            // eslint-disable-next-line
            <React.Fragment>{text}</React.Fragment>
        );
    });

    constructor() {
        super(ContentBlockName.string);
    }

    initParams = (parameters?: ParametersIO) => {
        const params = parameters ?? {};

        const stringFunc = paramKindCreationFuncs(ParamKind.string);
        if (stringFunc) {
            const stringParam = stringFunc({ parameterName: 'string', value: params.string });

            this.parameters = [stringParam];
        }
    };

    addChild = () => {
        // console.log('"string" content block can not has children');
    };
}
