import React from 'react';
import { Stack } from '@servicetitan/design-system';
import { TemplateEditorStore } from '../../../stores/template-editor.store';
import { TemplateList } from './components/template-list/template-list';
import { SelectedTemplate } from './components/selected-template/selected-template';
import { observer } from 'mobx-react';
import { SelectedBlockParameters } from './components/selected-block-parameters/selected-block-parameters';
import { SelectedTemplateJSONEditor } from './components/selected-template-json-editor/selected-template-json-editor';
import { SelectedTemplateStructure } from './components/selected-template-structure/selected-template-structure';
import { templateEditorStore } from '../../../stores';

@observer
export class TemplateEditor extends React.Component {
    private store = templateEditorStore; // : TemplateEditorStore = this.props.templateEditorStore!;

    render() {
        return (
            <Stack justifyContent="space-around">
                <div>
                    <TemplateList />
                    <hr />
                    {this.store.selectedTemplate && (
                        <React.Fragment>
                            <SelectedTemplateJSONEditor />
                            <SelectedTemplateStructure />
                            <hr />
                            <h3>Content block&apos;s parameters:</h3>
                            <SelectedBlockParameters />
                            <hr />
                        </React.Fragment>
                    )}
                </div>
                <SelectedTemplate />
            </Stack>
        );
    }
}
