import React, { useEffect } from 'react';
import { TemplateEditorStore } from '../../../../../stores/template-editor.store';
import { observer } from 'mobx-react';
import { createFromContentBlockIO } from '../../../../common/constants/content-blocks';
import { ContentBlockName } from '../../../../../api/test.api';
// import { Form } from '@servicetitan/design-system';
import { templateEditorStore as store } from '../../../../../stores';

export const SelectedTemplateJSONEditor: React.FC = observer(() => {

    if (!store.selectedTemplate) {
        return null;
    }

    const textArea = React.createRef<HTMLTextAreaElement>();

    const selectContentBlockInTextarea = () => {
        if (
            !store.selectedTemplate ||
            !store.selectedContentBlock ||
            !textArea ||
            !textArea.current
        ) {
            return;
        }
        const childrenChain = store.getChildrenChainFromTop(store.selectedContentBlock);
        const currentJSONText = store.getJSONTextForBlock(store.selectedTemplate.contentBlock);

        const copyBlock = createFromContentBlockIO(
            store.selectedTemplate.contentBlock.toIContentBlockIO
        );
        const selectedBlockInCopy = store.getChildBlockByChildrenChain(copyBlock, childrenChain);

        selectedBlockInCopy.contentBlockName =
            selectedBlockInCopy.contentBlockName === ContentBlockName.ReactFragment
                ? ContentBlockName.div
                : ContentBlockName.ReactFragment;
        const copyJSONText = store.getJSONTextForBlock(copyBlock);

        let startOfJSONTextChanges = 0;
        let rowsBefore = 0;
        for (let i = 0; i < currentJSONText.length; i += 1) {
            if (copyJSONText[i] === '\n') {
                rowsBefore += 1;
            }
            if (currentJSONText[i] !== copyJSONText[i]) {
                startOfJSONTextChanges = i;
                break;
            }
        }

        let rowsAfter = 0;
        for (let i = startOfJSONTextChanges; i < currentJSONText.length; i += 1) {
            if (copyJSONText[i] === '\n') {
                rowsAfter += 1;
            }
        }

        let endOfJSONTextChanges;
        for (
            endOfJSONTextChanges = startOfJSONTextChanges;
            endOfJSONTextChanges < currentJSONText.length;
            endOfJSONTextChanges += 1
        ) {
            if (currentJSONText[endOfJSONTextChanges] === '"') {
                break;
            }
        }
        textArea.current.focus();
        textArea.current.setSelectionRange(startOfJSONTextChanges, endOfJSONTextChanges);

        const topRow = Math.max(0, rowsBefore - 5);
        const scrollPercent = topRow / (rowsBefore + rowsAfter);
        textArea.current.scrollTop = textArea.current.scrollHeight * scrollPercent;
    };

    useEffect(() => {
        window.addEventListener(store.selectComponentBlockEventName, selectContentBlockInTextarea);
        return () => {
            window.removeEventListener(
                store.selectComponentBlockEventName,
                selectContentBlockInTextarea
            );
        };
    });

    const handleInputTextArea = (event: React.FormEvent<HTMLTextAreaElement>) => {
        store.handleChangeTextarea(event.currentTarget.value);
    };

    const handleBlur = () => {
        store.handleBlurTextarea();
    };

    const textareaValue = store.getJSONTextForBlock(store.selectedTemplate.contentBlock);

    return (
        // ToDO: change to 'Form.TextArea' after fixing ref bug
        <textarea
            // label="Template's JSON:"
            ref={textArea}
            value={store.textareaCopy !== undefined ? store.textareaCopy : textareaValue}
            onInput={handleInputTextArea}
            onBlur={handleBlur}
            cols={80}
            rows={25}
            wrap="off"
            style={{ overflow: 'auto' }}
        />
    );
});
