import React from 'react';
import { ParamKind, Parameter, ParameterProps } from '../block-parameters-definition';
import { observable, action } from 'mobx';
import { observer } from 'mobx-react';
import { ToggleSwitch } from '@servicetitan/design-system';

export class ParameterBoolean implements Parameter {
    paramKind = ParamKind.string;
    name = '';
    @observable value?: boolean;

    InputValueComponent: React.FC = observer(() => {
        return <ToggleSwitch label={this.name} checked={this.value} onChange={this.handleChange} />;
    });

    constructor(props: ParameterProps) {
        this.name = props.parameterName;
        if (props.value) {
            this.setValue(props.value);
        }
    }

    @action
    setValue(value: boolean) {
        this.value = value;
    }

    handleChange = (_1: string, checked: boolean) => {
        this.setValue(checked);
    };

    toObject() {
        return !this.value
            ? {}
            : {
                  [this.name]: this.value,
              };
    }
}
